<template>
    <div class="content-bg"  v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="登陆中"  element-loading-background="rgba(0, 0, 0, 0.8)">
      <div class="content-wapper">
        <!--<img src="../../../static/img/login_cx.png" />-->
        <img src="../../../static/img/logo_ay.png" />
        <div class="form-wapper">
          <el-form ref="form" :model="form" label-width="0">
            <el-form-item >
              <el-input size="small" v-model="form.phone" placeholder="账号"></el-input>
            </el-form-item>
            <el-form-item >
              <el-input type="password" size="small" v-model="form.password" placeholder="密码"></el-input>
            </el-form-item>
          </el-form>
          <el-button class="btn-submit" size="small" type="primary" @click="login">登录</el-button>
        </div>
      </div>
    </div>
    
</template>

<script>
	export default {
		name: "login",
		data() {
			return {
        fullscreenLoading:false,
			  form:{
          phone:'',
          password:''
        }
      }
		},
    methods:{
		  login(){
		    //遮罩
        this.fullscreenLoading = true;
        this.$nextTick(()=>{
          this.$get('user/login.do',{
            phone: this.form.phone,
            password:  this.form.password
          })
            .then((res) => {
              //关闭进度条
              if (res.success === 'true'){
                this.fullscreenLoading = false;
                sessionStorage.setItem("LoginUser", JSON.stringify(res.user));
                this.$router.push('/flowIndex');
              }else if(res.success==="false"){
                this.fullscreenLoading = false;
                this.$Message.error(res.message)
              }
            })
            .catch((err) => {
              //关闭进度条

            })
        })
      }
    }
	}
</script>

<style scoped>
  .content-bg{
    width: 100%;
    height: 100%;
    background: url('../../../static/img/bg.jpg') no-repeat;
    background-size: 100% 100%;
    font-size: 100px;
  }
  .content-wapper{
    height: 50vh;
    padding: 0 50px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    top: 5%;
  }
  .content-wapper>img{
    width: 100%;
  }
  .content-wapper .form-wapper{
    width: 90%;
    margin-top: 100px;
  }
  .btn-submit{
    width: 100%;
  }
</style>



